@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap')


body 
  display: flex
  position: relative
  width: 100%
  height: 100vh
  margin: 0 auto
  padding: 0
  background-image: url('./../images/ROAD.jpg')
  background-repeat: no-repeat
  background-size: cover
  max-width: 1920px
#root 
  margin: 0 auto
  width: 100%
  display: flex
  align-items: center
.video-text
  color: #3f353c
  font-family: Caveat
  font-weight: 400
  font-size: 1.5rem
  width: 90%
  text-align: center
  margin: 0 auto
  top: 125px
  left: 0px
  right: 0px
  z-index: 2
  position: absolute

.app
  width: 80%
  margin: 0 auto
  display: flex
  position: relative
  flex-direction: column
  align-items: center
  align-self: center

  & > h2
      color: #a30bc2
      font-family: Caveat
      font-weight: 400
      font-size: 2.5rem
      width: 90%
      text-align: center
      margin: 0 auto
      margin-top: 80px
      margin-bottom: 0px
      line-height: 3rem
  & > h3
      color: #3f353c
      font-family: Caveat
      font-weight: 400
      font-size: 2rem
      width: 90%
      text-align: center
      margin: 0 auto
      margin-top: 10px
      margin-bottom: 15px

  & > p
      color: #3f353c
      font-family: Caveat
      font-weight: 400
      font-size: 1.5rem
      width: 90%
      text-align: center
      margin: 0 auto
      margin-top: 0px
      margin-bottom: 5px
  & > button
      background-color: transparent
      font-family: Caveat
        
      font-size: 2rem
      border: 1px solid #DC143C .5
      cursor: pointer
      width: 80%
      margin: 0 auto
      margin-top: -20px
      color: #604a4f

  & > button:hover
      color: black

.container 
  position: relative
  overflow: hidden
  width: 100vw
  height: 100vh


.sidebar 
  height: 100%
  width: 40%
  position: absolute
  top: 0
  left: 0
  transition: transform 0.5s ease-in-out


.sidebar > div 
  height: 100%
  width: 100%
  display: flex
  flex-direction: column
  align-items: center
  justify-content: center
  color: #fff


.sidebar h1 
  font-size: 2rem
  margin-bottom: 10px
  margin-top: -30px


.main-slide 
  height: 100%
  position: absolute
  top: 0
  left: 40%
  width: 60%
  transition: transform 0.5s ease-in-out
  background-size: contain


.main-slide > div 
  background-repeat: no-repeat
  background-size: contain
  background-position: center center
  height: 100%
  width: 100%


button 
  background-color: #fff
  border: none
  color: #aaa
  cursor: pointer
  font-size: 16px
  padding: 15px


button:hover 
  color: #222


button:focus 
  outline: none


.container .controls button 
  position: absolute
  left: 40%
  top: 31%
  z-index: 100


.container .controls .down-button 
  transform: translateX(-100%)
  border-top-left-radius: 5px
  border-bottom-left-radius: 5px


.container .controls .up-button 
  transform: translateY(-100%)
  border-top-right-radius: 5px
  border-bottom-right-radius: 5px

